import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Message } from 'primeng/primeng';
import * as _ from 'lodash';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { selectGroupTopInfoBean, infoList } from './../bean/customRank.bean';
import { CusOperationHttpService } from "app/pages/tzb/custom/http/custom-operation/custom-operation.http.service";


@Component({
    selector: 'ranking-detail',
    templateUrl: './ranking-detail.component.html',
    styleUrls: ['./ranking-detail.component.scss'],
    providers: [CusOperationHttpService]
})
export class RankingDetailComponent implements OnInit {
    @Output() outValue: EventEmitter<any> = new EventEmitter<any>();
    @Input() inValue: any;
    @Input() inList: any;
    @Input() first: any;
    @Input() num: any;
    @Input() groupId: any;

    //公共
    tableMesg: any = tableMessage;
    // bean
    selectGroupTopInfoBean: selectGroupTopInfoBean = new selectGroupTopInfoBean();
    // 控件
    msgs: Message[] = [];//消息


    constructor(
        public httpService: CusOperationHttpService,
    ) { }
    ngOnInit() {

    }
    selectGroupTopInfo(i) {
        // 客户群榜单
        this.selectGroupTopInfoBean.groupId = _.clone(this.groupId);
        this.selectGroupTopInfoBean.type = i;
        this.httpService.selectGroupTopInfo(this.selectGroupTopInfoBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.selectGroupTopInfoBean = data;
                this.selectGroupTopInfoBean.infoList = data.infoList;
                this.inValue = _.clone(this.selectGroupTopInfoBean);
                this.inList = _.clone(this.selectGroupTopInfoBean.infoList);
                this.inValue.pageSize = _.clone(this.selectGroupTopInfoBean.pageSize);
                this.inValue.totalNum = _.clone(this.selectGroupTopInfoBean.totalNum);
            } else {
                this.msgs = [];
                data.returnCode.message ? data.returnCode.message : '榜单查询失败！';
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败！' }];
        })
    }
    return() {
        //   返回
        this.outValue.emit({ detailDisplay: false });
    }
    paginate(event) {
        // 分页
        this.selectGroupTopInfoBean.pageSize = event.rows;
        let currentPage = event.page + 1;
        this.selectGroupTopInfoBean.pageNum = currentPage;
        this.first = (currentPage - 1) * this.selectGroupTopInfoBean.pageSize;
        this.selectGroupTopInfo(this.num);
    }
}