import { Component, OnInit, Output, Input, EventEmitter, SimpleChanges, OnChanges } from '@angular/core';
import { SelectItem, Message, AutoCompleteModule } from 'primeng/primeng';
import * as _ from 'lodash';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { CusOperationHttpService } from '../../../../http/custom-operation/custom-operation.http.service';
import { debug } from 'util';

@Component({
    selector: 'freeGroup-owner-dialog',
    templateUrl: './freeGroup-owner-dialog.html',
    styleUrls: ['./freeGroup-owner-dialog.scss'],
    providers: [Commfunc]
})
export class FreeGroupOwnerDialog implements OnInit, OnChanges {

    @Output() outValue: EventEmitter<any> = new EventEmitter();
    @Input() inValue: any;
    @Input() groupId: any;
    @Input() freeGroupInfo: any;

    tableMesg: any = tableMessage;//表格提示信息
    msgs: Message[] = [];//提示信息

    is_change: boolean = false; //判断是否提交之前有没有发生改变）
    inint_inValue: any;//初始管理人员 （判断是否提交之前有没有发生改变）
    list: any = [];//表格展示数据
    staff_display: boolean = false;//选择人员模态框
    updateReason: any; //变更原因
    constructor(
        public httpService: CusOperationHttpService,
        public commfunc: Commfunc,
        public router: Router,
    ) {

    }

    ngOnInit() {
        console.log(this.inValue);
        this.inint_inValue = this.inValue;
        this.list = _.clone(this.inValue);
    }

    ngOnChanges() {

    }

    // 自选群-群主查询
    // selectFreeGroupInfo(groupId) {
    //     let params = {
    //         groupId: groupId
    //     }
    //     this.httpService.selectFreeGroupInfo(params).subscribe(data => {
    //         if (data.returnCode.code == 'success') {
    //             console.log(data);
    //         } else {
    //             this.msgs = [];
    //             data.returnCode.code ? data.returnCode.code : '查询失败';
    //             this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
    //         }
    //     }, err => {
    //         this.msgs = [];
    //         this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
    //     })
    // }

    // 增加人员
    showDialog() {
        if (this.list.length != 0) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '群主只能有一个，请先删除再添加！' });
            return false;
        }

        this.staff_display = true;
    }

    // 选择人员返回信息
    returnInfo(event) {
        this.staff_display = false;

        console.log(event);
        let arrTemp = [{
            ownerId: event[0].tellerId,
            ownerName: event[0].tellerName,
        }];
        this.list = _.clone(arrTemp);
    }

    // 提交
    submit() {
        //判断是否发生改变
        let is_change = true;
        if (this.list.length == 0) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '群主必须存在，请添加！' });
            return is_change = false;
        }

        this.inint_inValue.forEach(element1 => {
            this.list.forEach(element2 => {
                if (element1.ownerId == element2.ownerId) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '人员没有发生改变，请确认！' });
                    return is_change = false;
                }
            });
        });

        if (is_change) {
            this.updateCustGroupInfo();
        }
    }

    // 删除
    delArray(i) {
        this.list.splice(i, 1);
        this.list = _.clone(this.list);
    }

    // 修改自选群群主
    updateCustGroupInfo() {
        let params = {
            groupId: this.groupId,
            ownerId: this.list[0].ownerId,
            ownerName: this.list[0].ownerName,
            groupName: this.freeGroupInfo.groupName,
            orgId: this.freeGroupInfo.orgId,
            description: this.freeGroupInfo.description,
            updateReason: this.updateReason,
        }
        this.httpService.updateCustGroupInfo(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.outValue.emit(false);

                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务成功！' })
            } else {
                this.msgs = [];
                data.returnCode.code ? data.returnCode.code : '调用服务失败！';
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }]
            }
        }, err => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' })
        })
    }
    //取消
    close(){
        this.outValue.emit(false);
    }
}