import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { FreeGroupViewComponent } from './free-group-view.component';
import { CustomInformationDisplayComponent2 } from './custom-information-display2/custom-information-display2.component';
import { FreeImageQueryComponent } from "app/pages/tzb/custom/custom-marketing/group/free-group-view/free-image-query/free-image-query.component";

export const routes: Routes = [
  {
    path: '',
    component: FreeGroupViewComponent,//创建
  },{
    path: 'custom-information',
    component: CustomInformationDisplayComponent2,
  },{
    path: 'free-image-query',
    component: FreeImageQueryComponent,
  }
]

export const FreeGroupViewRouting: ModuleWithProviders = RouterModule.forChild(routes);
