import { Component, OnInit } from '@angular/core';
import { SelectItem, Message, ConfirmationService, ConfirmDialogModule } from 'primeng/primeng';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { tableMessage, CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { DatePipe } from '@angular/common';
import { TZB_HTTP_YXKQ } from 'app/@uisftech/common/service/http.constant';
import { CusOperationHttpService } from "app/pages/tzb/custom/http/custom-operation/custom-operation.http.service";
import { CustomImageQueryBean } from "app/pages/tzb/custom/custom-marketing/group/free-group-view/free-image-query/bean/free-image.bean";
@Component({
    selector: 'free-image-query',
    templateUrl: './free-image-query.component.html',
    styleUrls: ['./free-image-query.component.scss'],
    providers: [ConfirmationService, Commfunc, DatePipe, CusOperationHttpService]
})
export class FreeImageQueryComponent implements OnInit {
    title_1: any = '客户群影像查询';
    tabMesg: any = tableMessage;//表格中无数据时显示的内容
    first: number = 0;
    pageSize: number = 10;//每页条数
    totalNum: number = 0;
    pageNum: number = 1;
    ch: any = CALENDAR_CN;
    msgs: Message[] = [];
    total: string = '200';//总条数
    dataTable: any = [];
    upDateStart: string;//开始时间起
    upDateEnd: string;//开始时间止
    groupId: string;//客户群id
    customImageQueryBean: CustomImageQueryBean = new CustomImageQueryBean();
    personPage: boolean = false;
    personName: string;
    userInValue: string;
    constructor(
        public router: Router,
        public commfunc: Commfunc,
        public datePipe: DatePipe,
        public routerInfo: ActivatedRoute,
        public httpService: CusOperationHttpService,
        public confirmationService: ConfirmationService,
    ) { }
    ngOnInit() {
        this.groupId = this.routerInfo.snapshot.params['groupId'];
        this.title_1 = this.routerInfo.snapshot.params['groupName'] + '-' + this.title_1;
        this.customImageQueryBean.pageSize = 10;
        this.customImageQueryBean.pageNum = 1;
        this.first = 0;
        this.queryFirst(1);
    }
    //查询
    queryFirst(a) {
        this.pageSize = 10;
        this.pageNum = 1;
        this.first = 0;
        this.query(a);
    }
    //查询
    query(a) {
        this.customImageQueryBean.objId = this.groupId;
        this.customImageQueryBean.imageType = '1';
        this.customImageQueryBean.pageNum = this.pageNum;
        this.customImageQueryBean.pageSize = this.pageSize;
        this.customImageQueryBean.upDateStart = this.commfunc.transDateN(this.upDateStart);
        this.customImageQueryBean.upDateEnd = this.commfunc.transDateN(this.upDateEnd);
        this.httpService.selectImageInfo(this.customImageQueryBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.pageSize = data.pageSize;
                this.pageNum = data.pageNum;
                this.totalNum = data.totalNum;
                this.dataTable = data.imageInfo;
                if (data.imageInfo && data.imageInfo.length != '0') {
                    for (let item of data.imageInfo) {
                        item['groupName'] = data.groupName;
                        let param = {
                            tellerId: item.userId
                        }
                        this.httpService.queryTellerById(param).subscribe(data => {
                            item.userId = item.userId + "(" + data.tellerName + ")"
                        })
                    }
                }
                if (a != 1) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'success', summary: '提示', detail: '查询成功' });
                }

            } else {
                this.msgs = [];
                data.returnCode.message ? data.returnCode.message : '查询失败';
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败!' }];
        })
    }
    //重置
    reset() {
        this.upDateEnd = '';
        this.upDateStart = '';
        this.customImageQueryBean.upDateEnd = '';
        this.customImageQueryBean.upDateStart = '';
        this.customImageQueryBean.userId = '';
        this.customImageQueryBean.imageId = '';
        this.personName = '';
        this.query(2)
    }
    //日期选择
    showData(data) {
        let date = new Date();
        if (data == 1) {
            if (this.datePipe.transform(this.upDateStart, 'yyyy-MM-dd') > this.datePipe.transform(this.upDateEnd, 'yyyy-MM-dd')) {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期!' }];
                this.upDateEnd = '';
                this.upDateStart = '';
                return
            }
            if (this.upDateStart && this.upDateStart != '' && (!this.upDateEnd || this.upDateEnd == '')) {
                if (this.datePipe.transform(this.upDateStart, 'yyyy-MM-dd') > this.datePipe.transform(date, 'yyyy-MM-dd')) {
                    return
                } else {
                    this.upDateEnd = this.datePipe.transform(date, 'yyyy-MM-dd');
                }
            }
        } else if (data == 2) {
            if (this.datePipe.transform(this.upDateStart, 'yyyy-MM-dd') > this.datePipe.transform(this.upDateEnd, 'yyyy-MM-dd')) {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期!' }];
                this.upDateEnd = '';
                this.upDateStart = '';
                return
            }
            if (this.upDateEnd && this.upDateEnd != '' && (!this.upDateStart || this.upDateStart == '')) {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '请选择开始日期!' }];
            }
        }
    }
    //分页
    paginate(event) {
        this.pageSize = event.rows;
        this.pageNum = event.page + 1;
        this.first = event.page * this.pageSize;
        this.query(2);
    }
    showDetail(data) {
        //     let param ={
        //         userId : "0500",
        //         sysCode : '122',
        //         casServerLoginUrl : 'http://10.1.39.140:8011/searchPaymentTool',
        //         serverName : 'ywsn',
        //         validateType : '0',
        //         extraInfo : 'ywsn',
        //         encryptType : 'HS256'
        //     }
        // this.httpService.tokenApply(param).subscribe(data => {
        //   if (data.returnCode.code == 'success') {
        //     let tokens = data.tokenCode;
        //     window.open("http://10.1.97.166:9081/SunIAS_V7/SunIASRequestServlet.do?UID=CRM&OrgID=0117&OrgName=%E5%8F%B0%E5%B7%9E%E9%93%B6%E8%A1%8C%E6%B4%AA%E5%AE%B6%E6%94%AF%E8%A1%8C&OrgOwner=MOBILE&right=0100000&PWD=cyg&UserID=0500&UserName=%E9%82%B1%E6%8C%AF%E5%8D%8E&AppID=SA&info1=BUSI_SERIAL_NO:CN1300005981;OBJECT_NAME:APP1009;FILELEVEL:33;RIGHT:1111111;OBJECT_NAME:APP1009;QUERY_TIME:20180428")
        //   } else {
        //     this.msgs = [];
        //     this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
        //   }
        // }, error => {
        //   this.msgs = [];
        //   this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        // })
        //获取用户名及ID
        let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        let chName = JSON.parse(this.commfunc.getSessionDataCH('chName'));
        let orgID = commonHeader.orgId;
        let userId = commonHeader.userId;
        let orgName = chName.orgName;
        let userName = chName.userName;
        let time = new Date();
        let year = time.getFullYear();
        let month = time.getMonth() + 1 + '';
        let datas = time.getDate() + '';
        if (month.length < 2) {
            month = '0' + month;
        }
        if (datas.length < 2) {
            datas = "0" + datas
        }
        let QUERY_TIME = '' + year + month + data;
        window.open(TZB_HTTP_YXKQ + "SunIAS_V7/SunIASRequestServlet.do?UID=MOBILE&OrgID=" + orgID + "&OrgName=" + orgName + "&OrgOwner=" + orgID + "&right=0100000&PWD=cyg&UserID=" + userId + "&UserName=" + userName + "&AppID=SA&info1=BUSI_SERIAL_NO:" + data.imageId + ";OBJECT_NAME:APP1009;FILELEVEL:33;RIGHT:1111111;OBJECT_NAME:APP1009;QUERY_TIME:" + QUERY_TIME)
    }
    chooseWorker() {
        document.body.style.overflow = 'hidden';
        this.personPage = !this.personPage;
        this.userInValue = "0000"
    }
    perInfor(event) {
        document.body.style.overflow = 'auto';
        this.personPage = false;
        if (event.length > 0) {
            this.customImageQueryBean.userId = event[0].tellerId;
            this.personName = event[0].tellerName;
        }
    }
    returnCustGroupView() {
        // 返回客户群视图
        this.confirmationService.confirm({
            message: '确定返回客户群视图?',
            accept: () => {
                let groupId = this.groupId;
                this.router.navigate(['/pages/tzb/custom/custom-marketing/group/free-group-view', { groupId: groupId }]);
            }
        });
    }
}
