import { Component, OnInit } from '@angular/core';
import { SelectItem, Message, ConfirmationService, ConfirmDialogModule } from 'primeng/primeng';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { Observable } from 'rxjs/Rx';
import * as _ from 'lodash';
import { CommonHttpService } from "app/pages/tzb/common/http/common.http.service";
import { CusOperationHttpService } from '../../../http/custom-operation/custom-operation.http.service';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { tableMessage } from '../../../../../../@uisftech/common/constant/config';
import { Commfunc } from '../../../../../../@uisftech/common/service/commonFun';
@Component({
  selector: 'app-free-group-view',
  templateUrl: './free-group-view.component.html',
  styleUrls: ['./free-group-view.component.scss'],
  providers: [ConfirmationService, CusOperationHttpService, Commfunc, CommonHttpService],
})
export class FreeGroupViewComponent implements OnInit {

  ownerDisplay: boolean = false;//群主模态框
  orgDisplay; boolean = false;//所属机构模态框
  ownerDisplay2: boolean = false;//群主模态框是否出现2
  performanceDisplay: boolean;
  display: boolean = false;
  is_only_one;
  public userform: FormGroup;
  //公共
  tableMesg: any = tableMessage;
  selectCity: string;
  items: any;
  title: string = '工具栏';
  msgs: Message[] = [];
  // 客户群信息可编辑标志
  custGroupInfoeditFlag: boolean = true;
  // 是否主管群标志
  isOwnerFlag: boolean = true;
  freshProperty: boolean = true;//zhutui
  //shifouweihu

  mesg: any;//公共报文头信息

  whProperty: boolean = false;

  paramesInfo: any;//给子组件需要的保存接口参数
  deleteDisplay: boolean = false;

  //基本信息
  freeGroupInfo: any = {
    groupId: '',
    orgId: '',
    roupType: '',
    groupName: '',
    createOrgName: '',
    createOrgId: '',
    createdStamp: '',
    description: '',
    ownerName: ''
  };
  freeGroupInfoeditFlag: boolean = false;

  //中间量
  groupId: any = '';
  ownerName: any = '';
  groupName: any = '';
  ownerId: any = '';
  custGroupDemo: any = 0;

  inValue: any = {};//
  GroupRecProductInfoDisplay: boolean = false;
  GroupRecProductInfoDetailTitle: any = '主推产品';
  page: any = {};//bean
  title_1: any;
  lablel_left3: any;

  GroupAdminFlag: any = '';

  GroupAdminInfoDisplay: boolean = false; // 管理人员模态框
  GroupAdminInfoTitle: any;

  custGroupInfoeditFlag2: boolean = true;
  custGroupInfoeditFlag3: boolean = true;
  custGroupInfoeditFlag6: boolean = true;
  custGroupInfoeditFlag4: boolean = true;
  custGroupInfoeditFlag5: boolean = true;

  code: any;
  littleEvent: any;//
  groupType: any;//
  GroupLevel: any;//
  rankingDisplay: boolean = false; // 榜单
  moretDisplay: boolean = false; // 信息维护
  constructor(
    public httpService: CusOperationHttpService,
    public routerInfo: ActivatedRoute,
    public router: Router,
    public confirmationService: ConfirmationService,
    public commfunc: Commfunc,
    public fb: FormBuilder,
    public CommonHttpService: CommonHttpService
  ) {
    this.codeValues() //调用方法，获取全部码值
    //constructor获取所需要的码值,中括号中的变量是自己在原来码值表中的变量名！！！！
    this.littleEvent = this.code['BigEvent'];
    this.groupType = this.code['GroupTypeSave'];
    this.GroupLevel = this.code['GroupLevel']
    this.userform = fb.group({
      deleteR: ['', Validators.required],
    })
    // routerInfo.queryParams.subscribe(queryParams => {
    //   this.groupId = queryParams.groupId;
    // })
  }
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  ngOnInit() {
    this.groupId = this.routerInfo.snapshot.params['groupId'] || this.routerInfo.snapshot.queryParams['groupId'];
    this.paramesInfo = {
      roleType: 5,
      groupId: this.groupId
    }
    let session = window.sessionStorage;
    if (session.getItem('para')) {
      session.removeItem('para')
    }

    //默认查询一次
    this.selectFreeGroupInfo();
  }

  editGroupAdminInfo() {

  }
  //工具栏页面跳转
  toolbarClick(toolbar_index) {
    switch (toolbar_index) {
      // case 1: this.toAppraiserEvaluate(); break;//第三方评价 BID20046
      // case 2: this.toPrecreditApply(); break;//预授信   BID20045
      // case 3: this.queryCustomerList(); break;//短信发送  BID20047
      // case 4: this.jumpToYhll(); break;//优惠利率          BID20037
      // case 5: this.toGroupRating(); break;//群信用星级      BID20038
      // case 6: this.selectGroupRecProduct(); break;//主推产品 BID20039
      // case 7: this.editGroupAdminInfo(1); break;//创建人
      // case 8: this.editGroupAdminInfo(2); break;//创建机构
      // case 9: this.editGroupAdminInfo(3); break;//群评价人 BID20043
      // case 10: this.editGroupAdminInfo(4); break;//群介绍人 BID20044
      case 11: this.toGroupClientImport(); break;//群客户导入 BID20048
      case 12: this.delete2(); break;//群删除 BID20049
      case 13: this.tosearch(); break;//群地图
      case 14: this.groupPerformance(); break;//群业绩
      case 15: this.groupOwner(); break;//群主
      case 17: this.ranking(); break;//榜单
      case 18: this.customImageQuery(); break;//群影像
      // case 16: this.groupOwner(); break;//群名单高级查询   
      default: break;
    }
  }
  customImageQuery() {
    this.router.navigate(['/pages/tzb/custom/custom-group/custom-group-view/custom-image-query', { groupId: this.groupId, groupName: this.freeGroupInfo.groupName }]);
  }
  toGroupClientImport() {
    // 链接到客户群名单导入
    // this.confirmationService.confirm({
    // message: '确定进入客户名单导入页面?',
    // accept: () => {
    if (!this.display) {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '无此操作权限' });
      return false;
    }

    this.router.navigate(['/pages/tzb/custom/custom-group/group-client-import', { groupId: this.groupId, groupName: this.freeGroupInfo.groupName, custGroupView: '1', isFreeGroup: 1 }]);
    //   }
    // });
  }
  //群业绩模态框
  groupPerformance() {
    this.performanceDisplay = true;
  }
  //榜单
  ranking() {
    this.moretDisplay = false;
    this.rankingDisplay = true;
  }
  tosearch(){
    let a = this.commfunc.compileStr(this.groupId);
    this.router.navigate(['/pages/tzb/custom/custom-search/custom-group-search', { groupId: a,groupFlag : 2}]);
  }
  //群地图
  groupMap() {
    this.msgs = [];
    this.msgs = [{ severity: 'error', summary: '提示', detail: '暂未开放！' }];
  }
  //群主
  groupOwner() {
    if (this.mesg.extParam.managePermission != 'Y' && !this.display) {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '无此操作权限' });
      return false;
    }

    this.ownerDisplay = true;
    this.GroupAdminInfoTitle = '群主调整';
  }
  //所属机构
  createOrg() {
    this.freeGroupInfoeditFlag = true;
    this.custGroupInfoeditFlag6 = false;
    this.custGroupInfoeditFlag3 = false;
  }
  //选择机构
  selectOrg() {
    this.orgDisplay = true;
  }
  //机构树
  returnOrg(event) {
    console.log(event);
    this.orgDisplay = false;

    this.freeGroupInfo.orgName = event.orgName;
    this.freeGroupInfo.orgId = event.orgId;
  }

  // 查询
  selectFreeGroupInfo() {
    let params = {
      groupId: this.groupId
    }
    this.httpService.selectFreeGroupInfo(params).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.freeGroupInfo = data;

        if (this.freeGroupInfo.createdStamp && this.freeGroupInfo.createdStamp != '') {// 创建日期
          this.freeGroupInfo.createdStamp = this.freeGroupInfo.createdStamp.split(' ')[0];
        }
        if (data.orgId) {//所属机构
          this.queryPartyGroupListByPartyId(data.orgId, 1);
        }
        if (data.createOrgId) {//创建机构
          this.queryPartyGroupListByPartyId(data.createOrgId, 2);
        }

        if (data.ownerId && data.ownerId != '') {
          this.inValue = [{
            ownerId: data.ownerId,
            ownerName: data.ownerName
          }]
          console.log(this.inValue);
        }

        this.mesg = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (this.mesg.userId == data.ownerId) {
          this.display = true;
          this.ownerDisplay2 = true;
        }
      } else {
        this.msgs = [];
        data.returnCode.code ? data.returnCode.code : '调用服务失败！';
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
      }
    }, err => {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败！' }];
    })
  }

  //查询机构
  queryPartyGroupListByPartyId(orgId, i) {
    let params = {
      partyIdList: [orgId]
    }
    this.httpService.queryPartyGroupListByPartyId(params).subscribe(data => {
      if (data.returnCode.code == 'success') {
        switch (i) {
          case 1:
            this.freeGroupInfo['orgName'] = data.partyGroupList[0].groupName; break;
          case 2:
            this.freeGroupInfo['creatOrgName'] = data.partyGroupList[0].groupName; break;
        }

      } else {
        this.msgs = [];
        data.returnCode.code ? data.returnCode.code : '调用服务失败！';
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
      }
    }, err => {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败！' }];
    })
  }

  delete2() {
    if (!this.display) {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '无此操作权限' });
      return false;
    }

    let params = {
      freeGroupId: this.groupId,
    }
    this.confirmationService.confirm({
      message: '是否确认删除?',
      accept: () => {
        //客户群信息删除申请
        this.httpService.deleteFreeGroup(params).subscribe(data => {
          if (data.returnCode.code == 'success') {
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '提示', detail: '客户群删除成功！' });
            this.deleteDisplay = false;
            setTimeout(() => {
              window.opener.location.reload();
              self.close();
            }, 1000);
          } else {
            this.msgs = [];
            data.returnCode.code ? data.returnCode.code : '删除失败';
            this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
          }
        }, err => {
          this.msgs = [];
          this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败！' }];
        })
      }
    });

  }
  handleChange(event) {
    console.log(event)
    this.custGroupDemo = event.index;
    if (event.index != 0) {
      //  this.showYeJi = true;      
    } else {
      // this.showYeJi = false; 
    }
  }

  // 维护 和 取消
  editCustGroupInfo(index) {
    this.custGroupInfoeditFlag3 = false;

    if (index == 1) {
      this.whProperty = true;
      this.custGroupInfoeditFlag = false;
    } else if (index == 3) {
      this.whProperty = false;
      this.freeGroupInfoeditFlag = false;
      this.custGroupInfoeditFlag3 = true;
      this.custGroupInfoeditFlag = true;
      this.custGroupInfoeditFlag6 = true;
    }
  }
  // 查询维护
  moret() {
    this.moretDisplay = true;
  }
  // 查询维护返回
  returnGroupView() {
    this.moretDisplay = false;
  }
  //保存
  amend() {
    if (!this.freeGroupInfo.groupName || this.freeGroupInfo.groupName == '') {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '群名称不能为空！' }];
      return;
    }

    this.freeGroupInfoeditFlag = false;
    this.httpService.updateCustGroupInfo(this.freeGroupInfo).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.whProperty = false;
        this.custGroupInfoeditFlag = true;
        this.custGroupInfoeditFlag3 = true;
        this.custGroupInfoeditFlag6 = true;
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: '修改成功' });
        this.selectFreeGroupInfo();
      } else {
        this.msgs = [];
        data.returnCode.code ? data.returnCode.code : '修改失败';
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    })
  }
  returnGroupAdminInfo(event) {
    // console.log(event);
    this.ownerDisplay = false;

    this.selectFreeGroupInfo();
  }
  onHide() {
    this.selectFreeGroupInfo();
  }
  editFreeGroupInfo() {

  }
  ownerDialog() {
    this.ownerDisplay = false;
  }
  //按钮权限
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }
}
