export class selectGroupTopInfoBean{
    // 客户群榜单
    pageNum: number = 1;//当前页数
    pageSize: number = 10;//当前页数
    groupId: string = '';//群号
    type: string = '';//榜单类型
    totalNum: number = 1;//总条数
    infoList: Array<infoList> = [];//信息列表
}
export class infoList {
    //信息列表
    number: string = '';//序号
    custNo: string = '';//客户号
    custName: string = '';//客户名
    balance: string = '';//金额（万元）
}